form {
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.wrap {
    padding: 10px;
    height: 100vh;
}

.submit {
    padding: 10px;
    border: 1px solid #DDDDDD;
    background-color: aliceblue;
    border-radius: 15px;
    font-family: 'Allura', cursive;
}

.submit:hover,
.submit:focus {
    box-shadow: 0 0 50px 15px #82c6eb;
    font-weight: 600;
}

h1 {
    text-align: center;
}